<template>
  <div style="padding-bottom:20px;background:rgb(245, 245, 245)">
    <van-nav-bar
      :fixed="true"
      title="报告报表"
      @click-left="()=>{
          $router.go(-1)
         }"
      @click-right="()=>{
        show = true;
      }"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <van-icon name="filter-o" slot="right" size="20" />
    </van-nav-bar>
    <!-- <div class="legelList1">
      <div class="flexSearch">
        <div class="inputFlex">
          <input id="input" type="text" placeholder="请输入你想查看的内容" />
        </div>
        <div class="imageSearch" @click="Test">
          <img src="static/img/search.png" alt srcset />
        </div>
      </div>
    </div>-->
    <div class="legelList">
      <mu-list
        toggle-nested
        v-for="(item,index) in listContentReport"
        :key="index"
        style="padding:0"
        @click="clickBtnToWindow(item.path)"
      >
        <mu-list-item
          button
          :ripple="false"
          nested
          :open="open === 'send'"
          @toggle-nested="open = arguments[0] ? 'send' : ''"
          class="border-bottom"
        >
          <mu-list-item-title :style="'color:'+$store.state.color">{{item.name}}</mu-list-item-title>
          <mu-list-item-action>
            <mu-icon class="toggle-icon" color="#ccc" size="30" value="keyboard_arrow_right"></mu-icon>
          </mu-list-item-action>
        </mu-list-item>
      </mu-list>
    </div>
    <div v-show="showNodata===true" class="flexBox BoxStyle">
      <span>暂无数据...</span>
    </div>
    <van-popup v-model="show" position="bottom">
      <van-picker
        :columns="columns"
        show-toolbar
        @cancel="()=>{
        show = false
      }"
        @confirm="onConfirm"
      />
    </van-popup>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
let url = "http:47.112.16.168:";
import Vue from "vue";
import { Toast,Picker,Popup } from "vant";
Vue.use(Toast).use(Picker).use(Popup);
export default {
  name: "maintenanceChild3",
  data() {
    return {
      opened: false,
      open: "send",
      columns: ["月报", "周报", "日报"],
      value3: null,
      normal: {
        value1: "日报",
        value2: null
      },
      token: "",
      loading: false,
      listContentReport: [],
      value1: "dayReport",
      showNodata: false,
      show: false
    };
  },
  methods: {
    onConfirm(val) {
      this.normal.value1 = val;
      this.contentClick(val);
      this.show = false;
    },
    Test() {
      Toast("正处调试阶段，请稍后！");
    },
    selectOption() {},
    selectDataPicoptions() {
      this.opened = false;
      var _this = this;
      var pickerView = new PickerView({
        bindElem: classPicoptions, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.options, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择报表", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          _this.normal.value1 = selectArr[0];
          _this.contentClick(selectArr[0]);
          // _this.opened = true;
        }
      });
    },
    confirmQuery() {
      this.opened = false;
    },
    openBotttomSheet() {
      this.opened = true;
    },
    closeBottomSheet() {
      this.opened = false;
    },
    clickBtnToWindow(e) {
      window.open(url + e);
    },
    clickBtnTo(e) {
      this.$router.push({
        path: "/FilesDetails",
        query: { name: e, id: "Event" }
      });
    },
    async initDataList2(f) {
      var url = "/report";
      var pageNum = 1;
      var pageSize = 100000000;
      if (f) {
        var search = f;
      } else {
        var search = this.value1;
      }
      var res = await AppBace.request(
        url,
        "GET",
        {
          pageNum,
          pageSize,
          search
        },
        this.token
      );
      // console.log("日报", res);
      this.listContentReport = res.data.data.rows;
      if (this.listContentReport.length === 0) {
        this.showNodata = true;
      } else {
      }
    },
    async contentClick(e) {
      var f = "";
      switch (e) {
        case "月报":
          f = "monthReport";
          break;
        case "周报":
          f = "weekReport";
          break;
        case "日报":
          f = "dayReport";
          break;
      }
      await this.initDataList2(f);
      this.opened = false;
    },
    fullscreen() {
      this.loading = this.$loading();
    }
  },
  async mounted() {
    this.token = sessionStorage.getItem("token");
    await this.fullscreen();
    this.contentClick("日报");
    this.loading.close();
  }
};
</script>
<style scoped>
@import "../../../static/css/model.css";
.legelList {
  background: white;
  margin: 5px;
  border-radius: 10px;
  margin-top: 60px;
}
.legelList1 {
  position: fixed;
  top: 55px;
  width: 100%;
  background: #eefaeb;
  box-shadow: 0px 2px 0px #ccc;
  height: 60px;
  z-index: 9999;
  display: flex;
  align-items: center;
}
#input {
  border: none;
  width: 84%;
  outline: none;
  height: 32px;
  /* padding: 0 5px; */
  text-align: center;
}
.border {
  border-bottom: 1px solid #ccc;
}
.none {
  border: none;
}
.border-bottom {
  border-bottom: rgb(235, 234, 234) 1px solid;
}
.leftImage img {
  width: 25px;
  height: 25px;
}
.headerCon {
  position: fixed;
  top: 0;
  width: 100%;
}
.imageSearch img {
  width: 30px;
  height: 30px;
}
.imageSearch {
  display: flex;
  align-items: center;
}
#input {
  border: none;
  outline: none;
  width: 100%;
}
.inputFlex {
  flex: 1;
}
.flexSearch {
  display: flex;
  flex-direction: row;
  background: white;
  margin: 0 10px;
  border-radius: 10px;
  padding: 0 10px;
  width: 95%;
}
.flexBox {
  display: flex;
  justify-content: center;
  align-items: center;
}
.BoxStyle {
  color: grey;
  padding-top: 10px;
}
</style>



